import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AlertifyService } from '../_servicios/Alertify.service';
import { PageChangedEvent } from 'ngx-bootstrap/pagination';
import { DatosRegional } from '../_Models/DatosRegional';
import { DatosRegionalService } from '../_servicios/datos-regional.service';
@Component({
  selector: 'app-datos-regional',
  templateUrl: './datos-regional.component.html',
  styleUrls: ['./datos-regional.component.css']
})
export class DatosRegionalComponent implements OnInit {

  
  totalItems = 0;
  currentPage = 4;
  action = false;
  data1: any;
  datosRegionals: DatosRegional[];
  showBoundaryLinks = true;
  maxSize = 5;
  constructor( private route: ActivatedRoute, private alertify: AlertifyService , 
    private datosregionalService: DatosRegionalService) { }
  ngOnInit() {
    this.action = true;
    this.route.data.subscribe(data => {
      this.data1 = data['indicadores'];
      this.totalItems = this.data1.total;
      this.datosRegionals = this.data1.data;
      console.log(this.data1.data);
      this.action = false;
    });
  }

  pageChanged(event: PageChangedEvent): void {
    this.action = true;
    this.datosregionalService.getdatosRegionales(event.page).subscribe((indicadores: any) => {
      this.data1 =  indicadores;
      this.totalItems = this.data1.total;
      this.datosRegionals = this.data1.data;
      this.action = false;
    }, error => {
      this.action = false;
      this.alertify.error(error);
    });
  }

}
