import { Component, OnInit , ViewChild  } from '@angular/core';
import { ale } from "../../assets/maps/colombia.js";
import { ActivatedRoute, Router } from '@angular/router';

import { AlertifyService } from '../_servicios/Alertify.service';
import { EtcsService } from '../_servicios/etcs.service';
import { Indicador } from '../_models/Indicador.js';
import { IndicadoresService } from '../_servicios/indicadores.service';
import { TabDirective } from 'ngx-bootstrap/tabs';
import { TabsetComponent } from 'ngx-bootstrap';
import { Http } from '@angular/http';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-mapa',
  templateUrl: './mapa.component.html',
  styleUrls: ['./mapa.component.css']
})
export class MapaComponent implements OnInit {
  formatLabel(value: number | null) {
    if (!value) {
      return 0;
    }

    if (value >= 1000) {
      return Math.round(value / 1000) + 'k';
    }

    return value;
  }
  baseUrl = environment.apiUrl;
  @ViewChild('staticTabs') staticTabs: TabsetComponent;
  action = true;
  Datamapa:any;
  idname:string;
  indicadior: Indicador;
  Anios: any;
  //value: string;
  anio:string;
  selected:any;

  type: string;
  stacked: any[] = [];

  Accesibilidads: Indicador[];
  Accesibilidad = false;
Aceptabilidads: Indicador[];
Aceptabilidad= false;
Adaptabilidads: Indicador[];
Adaptabilidad= false;
Disponibilidads: Indicador[];
Disponibilidad= false;
indicadores: Indicador[];
indicador = false;
subindicador = false;
  subindicadores: Indicador[];
  constructor(  private route: ActivatedRoute,
     private alertify: AlertifyService ,
    private etcService: EtcsService,
    private router: Router,
    private indicadorService: IndicadoresService, private http: Http , ) { }

  ngOnInit() {
    this.getanios();
    this.randomStacked();
  }

  mapa(id:string ) {
  this.getdata(id);
    this.action= true;
    this.etcService.getDataMapa(id , this.anio).subscribe(dato => {
      this.Datamapa = dato;
      ale(this.Datamapa  );// funcion de javascrip
      this.action= false;
    }, error => {
      this.action = false;
      this.alertify.error(error);
    });
  }

  getdata(idin: any) {
    // tslint:disable-next-line:no-shadowed-variable
    this.indicadorService.getindicador(idin).subscribe(dato => {
      this.indicadior = dato;
    }, error => {
      this.action = false;
      this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    });
  }
  getanios(){
    this.indicadorService.getAnios().subscribe(dato => {
      this.Anios = dato;
    }, error => {
      this.action = false;
      this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    });
  }

  
  onSelect(data: TabDirective): void {
  
    this.anio= data.heading;
    this.mapa('ide');
  }

  randomStacked(): void {
    let types = ['danger',  'warning', 'success' ];
 let data =['33.33', '66.66', '100']
    this.stacked = [];
    for (let i = 0; i < 3 ; i++) {
      let index = i;
      let value = data[i];
      this.stacked.push({
        value,
        type: types[index],
        label: value + ' %'
      });
    }
  }

  getIndicadorTipo(Dimencion: string ) {
    this.http.get( this.baseUrl + 'indicadorDimencion/' + Dimencion).subscribe(response => {
      switch(Dimencion) {
        case 'Accesibilidad':
        this.Accesibilidads = response.json();
       if(this.Accesibilidad){
        this.Accesibilidad= false;
       }else {
        this.Accesibilidad=true;
       }
        break;
          case 'Aceptabilidad':
          this.Aceptabilidads = response.json();
         if(this.Aceptabilidad) {
          this.Aceptabilidad = false;
         }else {
          this.Aceptabilidad = true;
         }
       break;
          case 'Adaptabilidad':
          this.Adaptabilidads = response.json();
         if(this.Adaptabilidad){
          this.Adaptabilidad= false;
         }else{
          this.Adaptabilidad=true;
         }
   break;
          case 'Disponibilidad':
        this.Disponibilidads = response.json();
        if(this.Disponibilidad){
        this.Disponibilidad= false;
       } else {
       this.Disponibilidad = true;
       }
    break;
      }
      });
}
getvalidate(tipo: any) {
  switch(tipo) {
    case 0:
    if (this.subindicador) {
      this.subindicador = false;
    } else {
    this.getsubindicadores(tipo);
    this.subindicador = true;
    }
    break;
  }
}
getsubindicadores(tipo: number) {
  this.http.get( this.baseUrl + 'indicadorTipo/' + tipo).subscribe(response => {
    this.subindicadores = response.json();
    });
}
}
