import { Injectable } from '@angular/core';
import { Indicador } from '../_models/Indicador';
import { Observable } from 'rxjs/Observable';
import { Http, RequestOptions , Headers  } from '@angular/http';
import { environment } from '../../environments/environment';
import { map } from 'rxjs/operators';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { AuthHttp } from 'angular2-jwt';
import { Dato } from '../_Models/Dato';
@Injectable({
  providedIn: 'root'
})
export class DatosnacionalesService {
  baseUrl = environment.apiUrl;
constructor(private http: Http ,  private authHttp: AuthHttp ) { }


getindicadorNacional(id: string): Observable<any> {
  return this.http.get(this.baseUrl + 'indicadorNacional/' + id)
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}


getindicadorNacionalDataFull(Dimención: string): Observable<any> {
  return this.http.get(this.baseUrl + 'listindicador/' + Dimención)
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}

getsubindicadorDowloan(): Observable<any> {
  return this.http.get(this.baseUrl + 'Dowsubindicadores')
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}

getdataTable(Dimencion: string): Observable<any> {
  return this.http.get(this.baseUrl + 'tabledimenciones/' + Dimencion)
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}
getdataIndicador(id: string): Observable<any> {
  return this.http.get(this.baseUrl + 'tableIndicador/' + id)
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}
getdataSubINdicadorTable(): Observable<any> {
  return this.http.get(this.baseUrl + 'tableSubindica')
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}
getindicadorDowloan(id: string): Observable<any> {
  return this.http.get(this.baseUrl + 'DowLibreIndicador/' + id)
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}
getindicadorNacionalDescargar(Dimención: string): Observable<any> {
  return this.http.get(this.baseUrl + 'DowLibreDimenciones/' + Dimención)
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}
getsubindicadores(): Observable<any> {
  return this.http.get(this.baseUrl + 'datasubindicadores')
  .map(response => <any[]>response.json() )
  .catch(this.handlerError);
}

getdatoNacional(id: number): Observable<Dato> {
  return this.authHttp.get(this.baseUrl + 'datoNacionales/' + id)
  .map(response => <Dato>response.json() )
  .catch(this.handlerError);

}
getdatos(pag: number): Observable<any> {
  return this.authHttp.get(this.baseUrl + 'datosNacionales?page='+ pag)
  .map(response => <any[]>response.json())
  .catch(this.handlerError);
}

update(model: Dato) {
  //console.log(model);
  return  this.authHttp.post(this.baseUrl + 'datoNacionales/update', model  )
  .map(response => <any[]>response.json())
  .catch(this.handlerError);
}

private handlerError(error: any) {
  const applicationError = error.headers.get('Application-Error');
  if (applicationError) {
    return Observable.throw(applicationError);
  }
  const serverError = error.json();
  let modelStateErrors = '';
  if (serverError) {
    for (const key in serverError) {
      if (serverError[key]) {
        modelStateErrors += serverError[key] + '\n';
      }
    }
  }
  return Observable.throw(
    modelStateErrors || 'Server error'
  );
}
private requetOptions() {
  const headers = new Headers({'Content-type': 'application/json; charset=utf-8 ;, X-Requested-With": "XMLHttpReques'});
  return  new RequestOptions({headers: headers});
}

}
