import { Component, OnInit, ViewChild  } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { AlertifyService } from '../_servicios/Alertify.service';
import { DatosnacionalesService } from '../_servicios/datosnacionales.service';
import { PageChangedEvent } from 'ngx-bootstrap/pagination';
import { Dato } from '../_Models/Dato';
import { Indicador } from '../_models/Indicador';
import { ModalDirective } from 'ngx-bootstrap/modal';
@Component({
  selector: 'app-datos-general',
  templateUrl: './datos-general.component.html',
  styleUrls: ['./datos-general.component.css']
})
export class DatosGeneralComponent implements OnInit {
  @ViewChild(ModalDirective) staticModal: ModalDirective;
  totalItems = 0;
  currentPage = 4;
  action = false;
  data1: any;
  showBoundaryLinks = true;
  maxSize = 5;
  indicador: Indicador;
  datosNacionals: Dato[];
  datoNacional:  any = {};

  constructor( private route: ActivatedRoute, private alertify: AlertifyService ,
    private datosService: DatosnacionalesService,
    private router: Router) { }
  ngOnInit() {
    this.route.data.subscribe(data => {
      this.data1 = data['indicadores'];
      this.totalItems = this.data1.total;
      this.datosNacionals = this.data1.data;
 
    });
  }

  pageChanged(event: PageChangedEvent): void {
    this.action = true;
    this.datosService.getdatos(event.page).subscribe((indicadores: any) => {
      this.data1 =  indicadores;
      this.totalItems = this.data1.total;
      this.datosNacionals = this.data1.data;
      this.action = false;
   
    }, error => {
      this.action = false;
      this.alertify.error(error);
    });
  }

  getdatoNacional(idin: any) {
    // tslint:disable-next-line:no-shadowed-variable
    this.datosService.getdatoNacional(idin).subscribe(dato => {
      this.datoNacional = dato;
      //console.log(this.datoNacional);
      this.staticModal.show();
    }, error => {
      this.action = false;
      this.alertify.error(error);
    });
  }
  modificar() {
    this.datosService.update(this.datoNacional).subscribe(data => {
      this.alertify.success('bn');
     }, error => {
       this.alertify.error('Error no se puede modificar');
     });
}


}
