import { Component, OnInit , Input  } from '@angular/core';

@Component({
  selector: 'app-graficoAnosIndicador',
  templateUrl: './graficoAnosIndicador.component.html',
  styleUrls: ['./graficoAnosIndicador.component.css']
})
export class GraficoAnosIndicadorComponent implements OnInit {
 
  @Input() anio: any;
  @Input() dataG: any;
// grafico
estado = true;
barChartData = [];
barChartLabels = [] ;
barChartLegend = true;
barChartType = 'bar';
lineChartColors = [];
 barChartOptions = {
    scaleShowVerticalLines: true,
     responsive: true, 
     scales: {
        yAxes: [
          { ticks: 
            { beginAtZero:true,
              suggestedMax: 100
            } 
          }
        ]
      }
    };


  constructor() { }

  ngOnInit() {
   
      this.setGrafico(); 
        }


setGrafico() {
  let date= this.getRequest();
  if(date.length > 0){
    
    //console.log(date.length);
    //console.log(date);
    this.estado = true;
   let  color =[];
  let  array =[];
  for ( let i = 0; i < date.length; i++) {
    array.push({data: date[i] , label: ''});
  }
  this.barChartData = array;
  let name:string;
  for ( let i = 0; i < date.length; i++) {
    let Daton=[];
      name=date[i].idIncador;
      if(date[i].tipo >= 1){
        color.push({backgroundColor: date[i].color});
      }else{
        color.push({backgroundColor: '#00578B' });
      }
     Daton.push(date[i].totalNacionalEstandar  );
      let da = Daton;
         let clone = JSON.parse(JSON.stringify(this.barChartData));
         clone[i].data = da;
         clone[i].label = name;
      this.barChartData = clone;
    }
    this.lineChartColors=color;
  }else{
    this.estado = false;
  }
  }
getRequest(){// da formato 
  let dato=[];
  for ( let i = 0; i < this.dataG.length; i++) {
    for ( let j = 0; j < this.dataG[i].dataAnio.length; j++) {
      if ( this.dataG[i].dataAnio[j].anio == this.anio.anio ){
        let datos = 
          {idIncador: this.dataG[i].idIncador, 
            totalNacionalEstandar: this.dataG[i].dataAnio[j].totalNacionalEstandar, 
            totalNacional: this.dataG[i].dataAnio[j].totalNacional,
            anio: this.dataG[i].dataAnio[j].anio,
            color: this.dataG[i].dataAnio[j].color,
            tipo: this.dataG[i].dataAnio[j].Tipo,
          };
      dato.push(datos);
      }
  }
}
return dato;
}

}
