
import { Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { MapaComponent } from './mapa/mapa.component';
import { IndicadorListResolver } from './_resolvers/indicador-list.resolver';
import { IndicadoresComponent } from './indicadores/indicadores.component';
import { ErrorComponent } from './error/error.component';
import { NacionalComponent } from './indicadores/Nacional/Nacional.component';
import { RegionalComponent } from './indicadores/regional/regional.component';
import { EtcDetailResolver } from './_resolvers/etc-detail.resolver';
import {LoginComponent } from './login/login.component';
import { DatosGeneralComponent } from './datos-general/datos-general.component';
import { DatosNacionalListResolver } from './_resolvers/datos-nacional-list.resolver';
import { DatosRegionalListResolver } from './_resolvers/datos-reginal-list.resolver';
import { AuthGuard } from './_guards/auth.guard';
import { DatosRegionalComponent } from './datos-regional/datos-regional.component';
//import { InicioComponent } from './inicio/inicio.component';
export const appRoutes: Routes = [
{path: '', component: NacionalComponent},

{path: 'login', component: LoginComponent },

{
    path: '',
    runGuardsAndResolvers: 'always',
    canActivate: [AuthGuard],
    children: [
{path: 'home', component: HomeComponent },
{path: 'indicador', component: IndicadoresComponent , resolve: { indicadores: IndicadorListResolver }},
{path: 'datos/generales', component: DatosGeneralComponent ,  resolve: { indicadores: DatosNacionalListResolver } },
{path: 'datos/regionales', component: DatosRegionalComponent ,  resolve: { indicadores: DatosRegionalListResolver } },
]},
{path: 'general', component: NacionalComponent },
{path: 'territorial/:id', component: RegionalComponent  ,  resolve: { indicadores: EtcDetailResolver } },
{path: 'mapa', component:MapaComponent },
{path: 'error', component: ErrorComponent},
 // limitantes
{path: '**', redirectTo: '/error', pathMatch: 'full'}

];
