import { Resolve, Router, ActivatedRouteSnapshot } from '@angular/router';
import { Indicador } from '../_models/Indicador';
import { Injectable } from '@angular/core';
import { DatosRegionalService } from '../_servicios/datos-regional.service';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/of';
import { Observable } from 'rxjs/Observable';

@Injectable()

export class DatosRegionalListResolver implements Resolve<any[]> {
    constructor(private datosregionalService: DatosRegionalService , private router: Router) { }
    resolve(route: ActivatedRouteSnapshot): Observable<any[]> {
        return this.datosregionalService.getdatosRegionales(0).catch(error => {
            this.router.navigate(['/home']);
            return Observable.of(null);
        });
    }
}