import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { map} from 'rxjs/operators';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { Observable } from 'rxjs/Observable';
import { tokenNotExpired ,  JwtHelper } from 'angular2-jwt';
import { environment } from '../../environments/environment';
@Injectable()
export class AuthService {
  baseUrl = environment.apiUrl;
  userToken: any;
  decodedToken: any;
  name: any;
  jwtHelper: JwtHelper = new JwtHelper();
constructor(private http: Http) { }
login(model: any ) {
return this.http.post(this.baseUrl + 'login', model, this.requetOptions()).map((response: Response) => {
  const user = response.json();
  if (response.status == 401) {
    //console.log("NOT AUTHORIZED");
}
    if (user.success=== true) {
      localStorage.setItem('token', user.token );
      this.decodedToken = this.jwtHelper.decodeToken(user.token);
      this.userToken = user.token;
    }
  }).catch(this.handlerError);
}

register(model: any) {
  return  this.http.post(this.baseUrl + 'register', model , this.requetOptions()).catch(this.handlerError);
}

loggeedIn() {
  this.http.get(this.baseUrl + 'logout',  this.requetOptions()).catch(this.handlerError);
  return tokenNotExpired('token');
}

private requetOptions() {
  const headers = new Headers({'Content-type': 'application/json; charset=utf-8 ;, X-Requested-With": "XMLHttpReques'});
  return  new RequestOptions({headers: headers});
}
private handlerError(error: any) {
  const applicationError = error.headers.get('Application-Error');
  if (applicationError) {
    return Observable.throw(applicationError);
  }
  const serverError = error.json();
  let modelStateErrors = '';
  if (serverError) {
    for (const key in serverError) {
      if (serverError[key]) {
        modelStateErrors += serverError[key] + '\n';
      }
    }
  }
  return Observable.throw(
    modelStateErrors || 'Server error'
  );
}
}
