import { Injectable } from '@angular/core';
import { Indicador } from '../_models/Indicador';
import { Observable } from 'rxjs/Observable';
import { Http, RequestOptions , Headers  } from '@angular/http';
import { environment } from '../../environments/environment';
import { map } from 'rxjs/operators';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { AuthHttp } from 'angular2-jwt';
@Injectable()
export class IndicadoresService {
  baseUrl = environment.apiUrl;

constructor( private http: Http , private authHttp: AuthHttp) { }


getindicadores(): Observable<Indicador[]> {
 return this.http.get(this.baseUrl + 'indicadores')
 .map(response => <Indicador[]>response.json())
 .catch(this.handlerError);
  }

getindicador(id: any): Observable<Indicador> {
return this.http.get(this.baseUrl + 'indicador/' + id)
.map(response => <Indicador>response.json() )
.catch(this.handlerError);
}


getIndicadores(pag: number) : Observable<any> {
  return this.authHttp.get(this.baseUrl + 'indicadores?page='+ pag)
  .map(response => <any[]>response.json())
  .catch(this.handlerError);
}

getAnios() : Observable<any> {
  return this.http.get(this.baseUrl + 'anios')
  .map(response => <any>response.json())
  .catch(this.handlerError);
}



private handlerError(error: any) {
  const applicationError = error.headers.get('Application-Error');
  if (applicationError) {
    return Observable.throw(applicationError);
  }
  const serverError = error.json();
  let modelStateErrors = '';
  if (serverError) {
    for (const key in serverError) {
      if (serverError[key]) {
        modelStateErrors += serverError[key] + '\n';
      }
    }
  }
  return Observable.throw(
    modelStateErrors || 'Server error'
  );
}
}
