import { Component, OnInit } from '@angular/core';
import { ale } from "../../assets/maps/colombia.js";
import { ActivatedRoute, Router } from '@angular/router';

import { AlertifyService } from '../_servicios/Alertify.service';
import { EtcsService } from '../_servicios/etcs.service';
import { Indicador } from '../_models/Indicador.js';
import { IndicadoresService } from '../_servicios/indicadores.service';
import { TabDirective } from 'ngx-bootstrap/tabs';
import { TabsetComponent } from 'ngx-bootstrap';
import { Http } from '@angular/http';
import { environment } from '../../environments/environment';
@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements OnInit {
  Datamapa:any;
  idname:string;

  constructor( private route: ActivatedRoute,
    private alertify: AlertifyService ,
   private etcService: EtcsService,
   private router: Router,
   private indicadorService: IndicadoresService, private http: Http ,) { }

  ngOnInit() {
    this.mapa();
  }

  mapa() {
      this.etcService.getDataMapa('IDE' , '2015').subscribe(dato => {
        this.Datamapa = dato;
        ale(this.Datamapa  );// funcion de javascrip
      }, error => {
        this.alertify.error(error);
      });
    }
}
