import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
@Component({
  selector: 'app-etc',
  templateUrl: './etc.component.html',
  styleUrls: ['./etc.component.css']
})
export class EtcComponent implements OnInit {

  Etcs: any;
  constructor(private http: Http ) { }

  ngOnInit() {
    this.getetc();
  }



  getetc() {
    this.http.get('http://localhost/pruebas/ideweb/public/api/etc').subscribe(response => {
  this.Etcs = response.json();
  });
}
}
