  lineChartColors = [{
    lineTension: 0,
    fill: false,
    borderColor: 'green',
    backgroundColor: 'transparent',
    borderDash: [5, 5],
    pointBorderColor: 'green',
    pointBackgroundColor: 'green',
    pointRadius: 5,
    pointHoverRadius: 10,
    pointHitRadius: 30,
    pointBorderWidth: 2,
    pointStyle: 'rectRounded'
  }];
