import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Indicador } from '../_models/Indicador';
import { Http } from '@angular/http';
import { environment } from '../../environments/environment';
import { AlertifyService } from '../_servicios/Alertify.service';
import { IndicadoresService } from '../_servicios/indicadores.service';
import { PageChangedEvent } from 'ngx-bootstrap/pagination';
@Component({
  selector: 'app-indicadores',
  templateUrl: './indicadores.component.html',
  styleUrls: ['./indicadores.component.css']
})
export class IndicadoresComponent implements OnInit {
  totalItems = 0;
  currentPage = 4;
  action = false;
  showBoundaryLinks = true;
  maxSize = 5;
  indicadores: Indicador[];
  data1: any;
  constructor( private route: ActivatedRoute, private alertify: AlertifyService , private indicadorservice: IndicadoresService) { }
  ngOnInit() {
    this.action = true;
    this.route.data.subscribe(data => {
      this.data1 = data['indicadores'];
      this.totalItems = this.data1.total;
      this.indicadores = this.data1.data;
      this.action = false;
    });
  }
  pageChanged(event: PageChangedEvent): void {
    this.action = true;
    this.indicadorservice.getIndicadores(event.page).subscribe((indicadores: any) => {
      this.data1 =  indicadores;
      this.totalItems = this.data1.total;
      this.indicadores = this.data1.data;
      this.action = false;
    }, error => {
      this.action = false;
      this.alertify.error(error);
    });
  }
}
