import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Http, RequestOptions , Headers  } from '@angular/http';
import { environment } from '../../environments/environment';
import { map } from 'rxjs/operators';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { AuthHttp } from 'angular2-jwt';
@Injectable({
  providedIn: 'root'
})
export class DatosRegionalService {
  baseUrl = environment.apiUrl;
  constructor(private http: Http , private authHttp: AuthHttp) { }

  getdatosRegionales(pag: number): Observable<any> {
    return this.authHttp.get(this.baseUrl + 'datosregionales?page=' + pag)
    .map(response => <any[]>response.json())
    .catch(this.handlerError);
  }


  getindicadorDataFull(Dimención: string , id: number ): Observable<any> {
    return this.http.get(this.baseUrl + 'listindicadorRegional/' + Dimención + '/'+ id )
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }
  getdataTable(Dimencion: string , id: number): Observable<any> {
    return this.http.get(this.baseUrl + 'tabledimenciones/' + Dimencion +'/'+ id)
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }
  getdataIndicador(id: string, idET: number): Observable<any> {
    return this.http.get(this.baseUrl + 'tableIndicador/' + id +'/'+ idET)
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }
  getdataSubINdicadorTable(idET: number): Observable<any> {
    return this.http.get(this.baseUrl + 'tableSubindica/' + idET)
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }
  
  getindicadorDataFullDow(Dimención: string , id: number ): Observable<any> {
    return this.http.get(this.baseUrl + 'DowLibreDimenciones/' + Dimención + '/'+ id )
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }
  getindicador(  id: number , Dimención: string  ): Observable<any> {
    return this.http.get(this.baseUrl + 'indicadorRegional/' + id +'/' + Dimención  )
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }

  getdwoSubindicador(  id: number  ): Observable<any> {
    return this.http.get(this.baseUrl + 'Dowsubindicadores/' + id )
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }
  getsubindicadores(ide): Observable<any> {
    return this.http.get(this.baseUrl + 'datasubindicadoresRegionales/' + ide)
    .map(response => <any[]>response.json() )
    .catch(this.handlerError);
  }

  private handlerError(error: any) {
    const applicationError = error.headers.get('Application-Error');
    if (applicationError) {
      return Observable.throw(applicationError);
    }
    const serverError = error.json();
    let modelStateErrors = '';
    if (serverError) {
      for (const key in serverError) {
        if (serverError[key]) {
          modelStateErrors += serverError[key] + '\n';
        }
      }
    }
    return Observable.throw(
      modelStateErrors || 'Server error'
    );
  }
}
