import { Resolve, Router, ActivatedRouteSnapshot } from '@angular/router';
import { Etc } from '../_Models/Etc';
import { Injectable } from '@angular/core';
import { EtcsService } from '../_servicios/etcs.service';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/of';
import { Observable } from 'rxjs/Observable';

@Injectable()

export class EtcDetailResolver implements Resolve<Etc> {
    constructor(private etcServices: EtcsService, private router: Router) { }
    resolve(route: ActivatedRouteSnapshot): Observable<Etc> {
      return  this.etcServices.getetc(route.params['id']).catch(error => {
            this.router.navigate(['/territorial/3758']);
            return Observable.of(null);
        });
    }
}
