import { Injectable } from '@angular/core';
import { Etc } from '../_Models/Etc';
import { Observable } from 'rxjs/Observable';
import { Http, RequestOptions , Headers  } from '@angular/http';
import { environment } from '../../environments/environment';
import { map } from 'rxjs/operators';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

@Injectable({
  providedIn: 'root'
})
export class EtcsService {
  baseUrl = environment.apiUrl;
constructor(private http: Http) { }

getetcs(): Observable<Etc[]> {
  return this.http.get(this.baseUrl + 'etc')
  .map(response => <Etc[]>response.json())
  .catch(this.handlerError);
   }
getetc(id):  Observable<Etc> {

    return this.http.get(this.baseUrl + 'etc/' + id )
    .map(response => <Etc>response.json() )
    .catch(this.handlerError);
  }


  getDataMapa(ide:string , anio:string):  Observable<any> {
    return this.http.get(this.baseUrl + 'dataMapa/'+ ide +'/'+ anio )
    .map(response => <any>response.json() )
    .catch(this.handlerError);
  }

 private handlerError(error: any) {
   const applicationError = error.headers.get('Application-Error');
   if (applicationError) {
     return Observable.throw(applicationError);
   }
   const serverError = error.json();
   let modelStateErrors = '';
   if (serverError) {
     for (const key in serverError) {
       if (serverError[key]) {
         modelStateErrors += serverError[key] + '\n';
       }
     }
   }
   return Observable.throw(
     modelStateErrors || 'Server error'
   );
 }
}
