import { Component, OnInit  } from '@angular/core';
import { ActivatedRoute , Router} from '@angular/router';
import { Etc } from '../../_Models/Etc';
import { Indicador } from '../../_models/Indicador';
import { Http } from '@angular/http';
import { environment } from '../../../environments/environment';

import { IndicadoresService } from '../../_servicios/indicadores.service';
import { EtcsService } from '../../_servicios/etcs.service';
import { DatosRegionalService } from '../../_servicios/datos-regional.service';
import { AlertifyService } from '../../_servicios/Alertify.service';
import {ExcelService} from '../../_servicios/excel.service';
@Component({
  selector: 'app-regional',
  templateUrl: './regional.component.html',
  styleUrls: ['./regional.component.css']
})
export class RegionalComponent implements OnInit {

  texting = false;
  tittletable: string;
  tabledata: any;
  Dow=true;
  Estados:any;
  etc: Etc;
  Etcs: Etc[];
  indicador = false;
  subindicador = false;
  anio = [];
  estandarDato = [];
  baseUrl = environment.apiUrl;
  Datos = [];
  indicadores: Indicador[];
  subindicadores: Indicador[];
  idname:string ;
  barChartType: string = 'bar';
 barChartLegend = true;
  barChartLabels = [] ;
  selected: any;
  action = false;

  Accesibilidads: Indicador[];
  Accesibilidad = false;
Aceptabilidads: Indicador[];
Aceptabilidad= false;
Adaptabilidads: Indicador[];
Adaptabilidad= false;
Disponibilidads: Indicador[];
Disponibilidad= false;
indicadior: Indicador;
barChartData = [
  {data: this.estandarDato, label: this.idname }
];

subindicadorOption = false;
barChartOptions:any = {
  scaleShowVerticalLines: true,
   responsive: true, 
   scales: {
      yAxes: [
        { ticks: 
          { beginAtZero:true,
            suggestedMax: 100
          } 
        }
      ]
    }
  };


  lineChartColors:Array<any> = [
    { // grey
      backgroundColor: '#00578B',
      borderColor: 'rgba(148,159,177,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#00578B',
      pointHoverBackgroundColor: '#00578B',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    }
  ];

  constructor(private http: Http ,  private route: ActivatedRoute , 
    private etcServices: EtcsService , 
     private indicadorService: IndicadoresService,
     private  datosService: DatosRegionalService,  private alertify: AlertifyService, 
     private router: Router,  private excelService:ExcelService
  ) { }

  ngOnInit() {
    this.route.data.subscribe(data => {
      this.etc = data['indicadores'];
    });
    //this.loadetc();
    this.getDatinicial('IDE');
    this.getetc();
  }

  getetc() {
    this.http.get( this.baseUrl +'etc').subscribe(response => {
  this.Etcs = response.json();
  });
}
onclik($event, deviceValue) {
  this.router.navigate(['/territorial/' + deviceValue ]);
  this.getDatinicial('IDE');
}


getIndicadorTipo(Dimencion: string ) {
    this.http.get( this.baseUrl + 'indicadorDimencion/' + Dimencion).subscribe(response => {
      switch(Dimencion) {
        case 'Accesibilidad':
        this.Accesibilidads = response.json();
       if(this.Accesibilidad){
        this.Accesibilidad= false;
       }else {
        this.Accesibilidad=true;
       }
        break;
          case 'Aceptabilidad':
          this.Aceptabilidads = response.json();
         if(this.Aceptabilidad) {
          this.Aceptabilidad = false;
         }else {
          this.Aceptabilidad = true;
         }
       break;
          case 'Adaptabilidad':
          this.Adaptabilidads = response.json();
         if(this.Adaptabilidad){
          this.Adaptabilidad= false;
         }else{
          this.Adaptabilidad=true;
         }
   break;
          case 'Disponibilidad':
        this.Disponibilidads = response.json();
        if(this.Disponibilidad){
        this.Disponibilidad= false;
       } else {
       this.Disponibilidad = true;
       }
    break;
      }
      });
}

  getvalidate(tipo: any) {
    switch(tipo) {
      case 0:
      if (this.subindicador) {
        this.subindicador = false;
      } else {
      this.getsubindicadores(tipo);
      this.subindicador = true;
      }
      break;
          case 1:
           if(this.indicador) {
             this.indicador = false;
                 } else {
            this.getindicador(tipo);
            this.indicador = true;
          }
break;

    }
  }
getindicador(tipo: number) {
  this.http.get( this.baseUrl + 'indicadorTipo/' + tipo).subscribe(response => {
    this.indicadores = response.json();
    });
}
getsubindicadores(tipo: number) {
  this.http.get( this.baseUrl + 'indicadorTipo/' + tipo).subscribe(response => {
    this.subindicadores = response.json();
    });
}
// events
public chartClicked(e:any):void {

}

public chartHovered(e:any):void {
 
}


getdatosIndicadores(id: any) {
  this.subindicadorOption = false;
  this.barChartLegend= true;
  this.action = true;
   let estadar= [];
   let anio = [];
   let color: any;
   let name : string ;
   this.datosService.getindicador( this.etc.id , id ).subscribe((resultado: any) => {
    this.Datos = resultado;
    this.Estados={
      Estado: 'indicador',
      id: id
      };
      this.getdataTable();
this.action = false;
   for ( let i = 0; i < this.Datos.length; i++) {
                if(i ==0){
                  name=this.Datos[i].NombreIndicador;
                }
              anio.push(this.Datos[i].anio);
              estadar.push(this.Datos[i].totalregionalEstandar * 100);
              this.barChartData = [  {data: estadar, label: name}];
              let da = estadar;
              let clone = JSON.parse(JSON.stringify(this.barChartData));
              clone[0].data = da;
            this.barChartData = clone;
     }
            this.indicadior.NombreIndicador=name;
            this.barChartLabels = anio;
            color = this.getcolor(id);
            this.lineChartColors = [{ backgroundColor: color,}];
   }, error => {
     this.action = false;
     this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
   });
}
getcolor(id) {
  switch(id) {
    case 'Accesibilidad':
  return '#572972';
  case 'Aceptabilidad':
  return '#009AAC';
  case 'Adaptabilidad':
  return '#EBA02D';
  case 'Disponibilidad':
  return '#992787';
  default:
  return '#00578B';
}
}
getdata(idin: any) {
  // tslint:disable-next-line:no-shadowed-variable
  this.indicadorService.getindicador(idin).subscribe(dato => {
    this.indicadior = dato;

  });
}
getDatinicial(id){
  this.subindicadorOption = false;
  this.barChartLegend= true;
  this.getdata(id);
  this.action = true;
    let date: any;
    let color=[];
    this.datosService.getindicadorDataFull(id , this.etc.id ).subscribe((resultado: any) => {
      date = resultado; 
      this.Estados={
        Estado: 'Dimencion',
        id: id
        };
        this.getdataTable();
      let array = [];
      for ( let i = 0; i < date.length; i++) {
        array.push({data: date[i] , label: ''});
      }
      this.barChartData = array;
      let anio =[];
      let name:string;
      for ( let i = 0; i < date.length; i++) {
        let Daton=[];
        for ( let j = 0; j < date[i].datos.length; j++) {
          if( i==0){
            anio.push(date[i].datos[j].anio);
          }
          if(j ==0 ){
            name=date[i].datos[j].NombreIndicador;
            color.push({backgroundColor: date[i].datos[j].color});
          }
         Daton.push(date[i].datos[j].totalregionalEstandar *100);
        }
          let da = Daton;
             let clone = JSON.parse(JSON.stringify(this.barChartData));
             clone[i].data = da;
             clone[i].label = name;
          this.barChartData = clone;
      }
      this.lineChartColors=color;
      this.barChartLabels = anio;
      this.getIndicadorTipo(id);
      this.action = false;

    }, error => {
      this.action = false;
      this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    });
}

getsubi(){
  this.action = true;
   let anio = [];
   let date;
   let color =[];
    this.datosService.getsubindicadores(this.etc.id).subscribe((resultado: any) => {
      date = resultado;
      this.Estados={
        Estado: 'Sub-indicador'
        };
        this.getdataTable();
      let array = [];
      for ( let i = 0; i < date.length; i++) {
        array.push({data: date[i] , label: ''});
      }
      this.barChartData = array;
      let anio =[];
      let name:string;
      for ( let i = 0; i < date.length; i++) {
        let Daton=[];
        for ( let j = 0; j < date[i].datos.length; j++) {
          if( i==0){
            anio.push(date[i].datos[j].anio);
          }
          if(j ==0 ){
            name=date[i].datos[j].NombreIndicador;
            color.push({backgroundColor:'#00578B'});
          }
         Daton.push(date[i].datos[j].totalregionalEstandar *100);
        }
          let da = Daton;
             let clone = JSON.parse(JSON.stringify(this.barChartData));
             clone[i].data = da;
             clone[i].label = name;
          this.barChartData = clone;
      }
      this.subindicadorOption= true;
      this.lineChartColors=color;
      this.barChartLabels = anio;
      this.barChartLegend= false;
      this.action = false;
    }, error => {
      this.action = false;
      this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    });
}
exportAsXLSX():void {
  this.Dow=false;
  if(this.Estados.Estado=='Dimencion'){
    this.datosService.getindicadorDataFullDow(this.Estados.id , this.etc.id ).subscribe(Dato => {
    this.excelService.exportAsExcelFile(Dato, this.indicadior.NombreIndicador + '- Regional-' + this.etc.name );
    this.Dow=true;
  }, error => {
    this.action = false;
    this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    this.Dow=true;
  });
  }else if(this.Estados.Estado=='indicador'){
   this.datosService.getindicador( this.etc.id , this.Estados.id ).subscribe((resultado: any) => {
    this.excelService.exportAsExcelFile(resultado, this.indicadior.NombreIndicador + '- Nacional-Datos');
    this.Dow=true;
  }, error => {
    this.action = false;
    this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    this.Dow=true;
  });
  }else if(this.Estados.Estado=='Sub-indicador'){
    this.datosService.getdwoSubindicador(this.etc.id).subscribe((resultado: any) => {
     this.excelService.exportAsExcelFile(resultado, 'sub-Indicadores- regional-' + this.etc.name);
     this.Dow=true;
    }, error => {
      this.Dow=true;
      this.action = false;
     this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
   });
   }
  }
  getdataTable(){
    this.tittletable='Total territorial';
    if(this.Estados.Estado=='Dimencion'){
      this.datosService.getdataTable(this.Estados.id , this.etc.id).subscribe(Dato => {
        this.tabledata=  Dato;
    }, error => {
      this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    });
    }else if(this.Estados.Estado=='indicador'){
     this.datosService.getdataIndicador(this.Estados.id, this.etc.id ).subscribe((resultado: any) => {
      this.tabledata=  resultado;
    }, error => {
      this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
    });
    }else if(this.Estados.Estado=='Sub-indicador') {
      this.datosService.getdataSubINdicadorTable(this.etc.id ).subscribe((resultado: any) => {
        this.tabledata=  resultado;
      }, error => {
       this.alertify.error('Error no hemos podido procesar su solicitud, intenta de nuevo');
      });
    }
  }

}
