import { Resolve, Router, ActivatedRouteSnapshot } from '@angular/router';
import { Indicador } from '../_models/Indicador';
import { Injectable } from '@angular/core';
import { DatosnacionalesService } from '../_servicios/datosnacionales.service';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/of';
import { Observable } from 'rxjs/Observable';

@Injectable()

export class DatosNacionalListResolver implements Resolve<any[]> {
    constructor(private datosnacionalesService: DatosnacionalesService , private router: Router) { }
    resolve(route: ActivatedRouteSnapshot): Observable<any[]> {
        return this.datosnacionalesService.getdatos(0).catch(error => {
            this.router.navigate(['/home']);
            return Observable.of(null);
        });

    }
}
