import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../_servicios/auth.service';
import { AlertifyService } from '../_servicios/Alertify.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  model: any = {};
action = false;
  constructor(private authService: AuthService, private alertify: AlertifyService, private router: Router) { }

  ngOnInit() {
    if( this.loggedIn()) {
      this.alertify.error('Error usuario ya se encuentra logeado');
      this.router.navigate(['/home']);
    }
  }

  login() {
    if ( !this.action){
    this.action = true;
    this.authService.login(this.model).subscribe(data => {
    this.alertify.success('logeado corecctamente');
    this.action= false;
   }, error => {
     this.alertify.error('Error usuario erroneos');
     this.action= false;
   }, ( ) => {
     this.router.navigate(['/home']);
   });
  }
   }
   logout() {
     this.authService.userToken = null;
     localStorage.removeItem('token');
     this.alertify.message('cerro Sessión correctamente');
     this.router.navigate(['/general']);
   }
   loggedIn() {
   return this.authService.loggeedIn();
   }
}
