import { Resolve, Router, ActivatedRouteSnapshot } from '@angular/router';
import { Indicador } from '../_models/Indicador';
import { Injectable } from '@angular/core';
import { IndicadoresService } from '../_servicios/indicadores.service';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/of';
import { Observable } from 'rxjs/Observable';

@Injectable()

export class IndicadorListResolver implements Resolve<any[]> {
    constructor(private indicadoresService: IndicadoresService, private router: Router) { }
    resolve(route: ActivatedRouteSnapshot): Observable<any[]> {
        return this.indicadoresService.getIndicadores(0).catch(error => {
            this.router.navigate(['/home']);
            return Observable.of(null);
        });

    }
}
